const Video = ({ title, videoEmbed }) => {
    // directly using the returned html embed code is unsafe.
    // If the server responce was spoofed an attacker could 
    // inject any html or js they wanted


    /*
        TODO
        get the diffrence between a live video and an archived live video
        also seduled videos and shit

        if video is seceduled 
        archived: false
        streaming: false

        if live video is archived
        {streaming: false, archived: true}

        And im assuming if its currently live
        {streaming: true, archived: false}

    */
    if(videoEmbed === null) return (<></>);

    let videoParams = videoEmbed.substring(videoEmbed.indexOf("https://player.vimeo.com/video/") + 31);
    videoParams = videoParams.substring(0, videoParams.indexOf("\""));
    // width="640" height="360" frameBorder="0" 
    // 17 / 9

    //<iframe title="vimeo-player" src="https://player.vimeo.com/video/640108690?h=ff52d55639" width="640" height="360" frameborder="0" allowfullscreen></iframe>

    return(
        <div className="videoWrapper">
            <iframe className="video" title={title} src={`https://player.vimeo.com/video/${videoParams}`} frameborder="0" width="100%" height="100%" allowFullScreen></iframe>
        </div>
    );
}

export default Video