import { useState, useEffect } from "react";
import ArchivedVideos from "./ArchivedVideos";
import LiveVideos from "./LiveVideos";
import Video from "./Video";

const token = "5d5c157df9f684e811d74a303341da43";
const sconaChannelId = "82197283";

const VideoSections = () =>{ 
    const [error, setError] = useState(null);
	const [isLoaded, setIsLoaded] = useState(false);
	const [videos, setVideos] = useState([]);

	useEffect(() => {
		fetch(`https://api.vimeo.com/users/${sconaChannelId}/videos?direction=desc`, {
			method:'GET', 
			headers: {'Authorization': 'Bearer ' + token}
		})
		.then(res => res.json())
		.then(
            (result) => {
                setVideos(result.data);
                setIsLoaded(true);
                console.log(result);
            },

            (error) => {
                setIsLoaded(true);
                setError(error);
                console.log("ERROR: " + error);
            }
		)
	}, [])

    function getContent(live) {
        // legit one word diffrent between the first two returns
        // can I simplify???

        if(isLoaded && live){
            return (
                <LiveVideos data={videos}/>
                // <div className="videoGrid">
                //     {videos.map((video, i) => (
                //         video.type === "live" && (video.embed.badges.live.streaming || (!video.embed.badges.live.streaming && !video.embed.badges.live.archived)) ? <Video key={i} title={video.name} videoEmbed={video.embed.html}/> : null
                //     ))}
                // </div>
            );
        }else if(isLoaded){
            return (
                <ArchivedVideos data={videos}/>
            );
        }else if(!isLoaded){
            return <p className="transparentText">Loading...</p>;
        }else if(error){
            return <p>Error: {error}</p>;
        }
    }

    return(
        <div>
            <h1 className="subtitle">Live Videos</h1>
            {/* <hr/> */}

            { getContent(true) }

            <h1 className="subtitle">Archived Videos</h1>
            {/* <hr/> */}

            { getContent(false) }
        </div>

        /*  if (error) {
                return <div>Error: {error.message}</div>;
            } else if (!isLoaded) {
                return <div>Loading...</div>;
            } else {
                return (
                    <div className="videoGrid">
                        {videos.map((video, i) => (
                            <Video key={i} title={video.name} videoEmbed={video.embed.html}/>
                        ))}
                    </div>
                );
            }
        */
    );
}

export default VideoSections;