import Video from "./Video";

const LiveVideos = ({data}) => {
    let dispVideos = [];
    let noVideos;

    for(let i = 0; i < data.length; i++){
        let isLive = data[i].embed.badges.live.streaming;
        let isArchived = data[i].embed.badges.live.archived;

        if(isLive && !isArchived){
            dispVideos.unshift(data[i]);
        }else if(data[i].type === "live" && (isLive || (!isLive && !isArchived))){
            dispVideos.push(data[i]);
        }
    }

    if(dispVideos.length === 0){
        noVideos = <h2 className="transparentText">No live videos</h2>;
    }

    return (
        <div className="videoGrid">
            {dispVideos.map((video, i) => (
                <Video key={i} title={video.name} videoEmbed={video.embed.html}/>
            ))}
            
            { noVideos }
        </div>
    );
}

export default LiveVideos